@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;900&display=swap);
:root {
  --header-height: 3.5rem;

  /* colors */

  --hue: 14;
  --first-color: hsl(var(--hue), 91%, 54%);
  --first-color-alt: hsl(var(--hue), 91%, 50%);
  --title-color: hsl(var(--hue), 4%, 100%);
  --text-color: hsl(var(--hue), 4%, 85%);
  --text-color-light: hsl(var(--hue), 4%, 55%);
  --body-color: linear-gradient(
    90deg,
    hsl(104, 28%, 50%) 0%,
    hsl(58, 28%, 40%) 100%
  );
  --container-color: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );

  /* Fonts and typography */

  --body-font: "Poppins", sans-serif;
  --biggest-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;
  --smaller-font-size: 0.75rem;

  /* Font weight */

  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;

  /* z-index */

  --z-tooltip: 10;
  --z-fixed: 100;

  /* responsove rypography */
}

@media screen and (min-width: 992px) {
  :root {
    --biggest-font-size: 4rem;
    --h1-font-size: 2.5rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: 0.875rem;
    --smaller-font-size: 0.815rem;
  }
}

/* BASE */

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: 3.5rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  font-family: "Poppins", sans-serif;
  font-family: var(--body-font);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 50%) 0%,
    hsl(58, 28%, 40%) 100%
  );
  background: var(--body-color);
  color: hsl(14, 4%, 85%);
  color: var(--text-color);
}

h1,
h2,
h3,
h4 {
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
  font-size: bold;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

button,
input {
  border: none;
}

button {
  cursor: pointer;
  font-family: "Poppins", sans-serif;
  font-family: var(--body-font);
}

.section {
  padding: 4.5rem 0 2rem;
  text-align: center;
}

.section_title {
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-2);
  text-align: center;
}

.container {
  max-width: 968px;
  margin: 0 auto;
}

.main {
  overflow: hidden; /* for animation */
}

.grid {
  display: grid;
}

/* HEADER */

.header {
  width: 100%;
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 50%) 0%,
    hsl(58, 28%, 40%) 100%
  );
  background: var(--body-color);
  position: fixed;
  transition: 0.3s;
  top: 0;
  left: 0;
  z-index: 100;
  z-index: var(--z-fixed);
}

.header__scroll {
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );
  background: var(--container-color);
}

/* NAV */

.nav {
  height: 3.5rem;
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

.nav__logo {
  display: flex;
  align-items: center;
  grid-column-gap: 0.5rem;
  -webkit-column-gap: 0.5rem;
          column-gap: 0.5rem;
}

.nav__logo-img {
  width: 1.25rem;
}

.nav__link,
.nav__logo,
.nav__toggle,
.nav__close {
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
}

.nav__toggle {
  font-size: 1.25rem;
  cursor: pointer;
}

@media screen and (max-width: 767px) {
  .nav__menu {
    position: fixed;
    width: 100%;
    background: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );
    background: var(--container-color);
    top: -150%;
    left: 0;
    padding: 3.5rem 0;
    transition: 0.4s;
    z-index: 100;
    z-index: var(--z-fixed);
    border-radius: 0 0 1.5rem 1.5rem;
  }
}

.nav__img {
  width: 100px;
  position: absolute;
  top: 0;
  left: 0;
}

.nav__close {
  position: absolute;
  top: 0.3rem;
  right: 0.9rem;
  cursor: pointer;
}

.nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-row-gap: 1.5rem;
  row-gap: 1.5rem;
}

.nav__link {
  text-transform: uppercase;
  font-weight: bold;
  transition: 0.4s;
}

.nav__link:hover {
  color: hsl(14, 4%, 85%);
  color: var(--text-color);
}

.show__menu {
  top: 0;
}

/* home */

.home__content {
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

.home__group {
  position: relative;
  padding-top: 2rem;
  display: grid;
}

.home__img {
  height: 250px;
  justify-self: center;
}

.home__indicator {
  width: 8px;
  height: 8px;
  background-color: hsl(14, 4%, 100%);
  background-color: var(--title-color);
  border-radius: 50%;
  position: absolute;
  top: 7rem;
  right: 2rem;
}

.home__indicator::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 48px;
  background-color: hsl(14, 4%, 100%);
  background-color: var(--title-color);
  top: -3rem;
  right: 45%;
}

.home__details-img {
  position: absolute;
  right: 0.5rem;
}

.home__details-title,
.home__details-subtitle {
  display: block;
  font-size: 0.813rem;
  font-size: var(--small-font-size);
  text-align: right;
}

.home__subtitle {
  font-size: 1rem;
  font-size: var(--h3-font-size);
  text-transform: uppercase;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-1);
}

.home__title {
  font-size: 2rem;
  font-size: var(--biggest-font-size);
  font-weight: bold;
  line-height: 109%;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-1);
}

.home__description {
  margin-bottom: var(--md-1);
}

.home__buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
  margin-top: var(--mb-1);
}

.button {
  display: inline-block;
  background-color: hsl(14, 91%, 54%);
  background-color: var(--first-color);
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
  padding: 1rem 1.75rem;
  border-radius: 0.5rem;
  font-weight: var(--font-medium);
  -webkit-transform: 0.3s;
          transform: 0.3s;
}

.button:hover {
  background-color: hsl(14, 91%, 50%);
  background-color: var(--first-color-alt);
}

.button--ghost {
  border: 2px solid;
  background-color: transparent;
  border-radius: 3rem;
  padding: 0.75rem 1.5rem;
}

.button--ghost:hover {
  background: none;
}

.button--link {
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
}

.button--flex {
  display: inline-flex;
  align-items: center;
}

.swiper-container {
  display: flex;
  flex-direction: column-reverse;
}

.swiper-pagination {
  position: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
}

.swiper-pagination-bullet {
  background: hsl(14, 4%, 100%);
  background: var(--title-color);
}

.swiper-pagination-bullet-active {
  width: 1.5rem;
  height: 5px;
  border-radius: 0.5rem;
}

.category__container {
  display: flex;
  flex-wrap: wrap;
}

.category__data {
  width: 50%;
  padding: 20px;
}

.category__img {
  width: 120px;
  margin-bottom: 0.75rem;
  margin-bottom: var(--mb-0-75);
  transition: 0.3s;
}

.category__title {
  margin-bottom: 0.25rem;
  margin-bottom: var(--mb-0-25);
}

.category__data:hover .category__img {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

/* ABOUT */

.about__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.about__data {
  text-align: center;
}

.about__description {
  margin-bottom: 2rem;
  margin-bottom: var(--mb-2);
}

.about__img {
  width: 200px;
  justify-self: center;
}

/* TRICK */

.trick__container {
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding-top: 1rem;
}

.trick__content {
  position: relative;
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );
  background: var(--container-color);
  border-radius: 1rem;
  padding: 1.5rem 0;
  text-align: center;
  overflow: hidden;
}

.trick__img {
  width: 90px;
  transition: 0.3s;
}

.trick__subtitle,
.trick__price {
  display: block;
}

.trick__subtitle {
  font-size: 0.75rem;
  font-size: var(--smaller-font-size);
  margin-bottom: 0.5rem;
  margin-bottom: var(--mb-0-5);
}

.trick__title,
.trick__price {
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
  font-weight: var(--font-medium);
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
}

.trick__button {
  display: inline-flex;
  padding: 0.6rem 1rem;
  border-radius: 0.25rem 0.25rem 0.75rem 0.25rem;
  position: absolute;
  transition: 0.4s;
  right: -4rem;
  bottom: 0;
}

.trick__content:hover .trick__img {
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem);
}

.trick__content:hover .trick__button {
  right: 0;
}

/* DISCOUNT */

.discount__container {
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );
  background: var(--container-color);
  border-radius: 1rem;
  padding: 2.5rem 0 1.5rem;
  grid-row-gap: 0.75rem;
  grid-row-gap: 0.75rem;
  row-gap: 0.75rem;
}

.discount__title {
  font-size: 1.25rem;
  font-size: var(--h2-font-size);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-2);
}

.discount__img {
  width: 200px;
  justify-self: center;
}

/* NEW ARRIVALS */

.new__container {
  padding-top: 1rem;
}

.new__img {
  width: 120px;
  margin-bottom: 0.5rem;
  margin-bottom: var(--mb-0-5);
  transition: 0.3s;
}

.new__content {
  position: relative;
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );
  background: var(--container-color);
  width: 242px;
  padding: 2rem 0 1.5rem 0;
  text-align: center;
  overflow: hidden;
  border-radius: 0.75rem;
}

.new__tag {
  position: absolute;
  top: 8%;
  left: 8%;
  background: hsl(14, 91%, 54%);
  background: var(--first-color);
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
  font-size: 0.813rem;
  font-size: var(--small-font-size);
  padding: 0.25rem 0.5rem;
}

.new__title {
  font-size: 0.938rem;
  font-size: var(--normal-font-size);
  font-weight: 600;
}

.new__subtitle {
  display: block;
  font-size: 0.813rem;
  font-size: var(--small-font-size);
  margin-bottom: 0.5rem;
  margin-bottom: var(--mb-0-5);
}

.new__prices {
  display: inline-flex;
  align-items: center;
  grid-column-gap: 0.5rem;
  -webkit-column-gap: 0.5rem;
          column-gap: 0.5rem;
}

.new__price {
  font-weight: 600;
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
}

.new__discount {
  color: hsl(14, 91%, 54%);
  color: var(--first-color);
  font-size: 0.813rem;
  font-size: var(--small-font-size);
  text-decoration: line-through;
  font-weight: 600;
}

.new__button {
  display: inline-flex;
  padding: 0.5rem;
  border-radius: 0.25rem 0.25rem 0.75rem 0.25rem;
  position: absolute;
  bottom: 0;
  right: -3rem;
  transition: 0.3s;
}

.new__content:hover .new__img {
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem);
}

.new__content:hover .new__button {
  right: 0;
}

/* NEWSLETTER */

.newsletter__description {
  text-align: center;
  margin-bottom: 1.5rem;
  margin-bottom: var(--mb-1-5);
}

.newsletter__form {
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );
  background: var(--container-color);
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  border-radius: 0.75rem;
}

.newsletter__input {
  width: 70%;
  padding: 0 0.5rem;
  background: none;
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
}

.newsletter__input::-webkit-input-placeholder {
  color: hsl(14, 4%, 85%);
  color: var(--text-color);
}

.newsletter__input:-ms-input-placeholder {
  color: hsl(14, 4%, 85%);
  color: var(--text-color);
}

.newsletter__input::placeholder {
  color: hsl(14, 4%, 85%);
  color: var(--text-color);
}

/* FOOTER */

.footer {
  position: relative;
  overflow: hidden;
  text-align: left;
}

.footer__img-one,
.footer__img-two {
  position: absolute;
  transition: 0.3s;
}

.footer__img-one {
  width: 100px;
  top: 6rem;
  right: -2rem;
}

.footer__img-two {
  width: 150px;
  bottom: 4rem;
  right: 4rem;
}

.footer__img-one:hover,
.footer__img-two:hover {
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem);
}

.footer__container {
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.footer__logo {
  display: flex;
  align-items: center;
  grid-column-gap: 0.5rem;
  -webkit-column-gap: 0.5rem;
          column-gap: 0.5rem;
  margin-bottom: 1rem;
  margin-bottom: var(--mb-1);
  font-weight: 600;
  color: hsl(14, 4%, 85%);
  color: var(--text-color);
}

.footer__logo-img {
  width: 20px;
}

.footer__description {
  margin-bottom: 2.5rem;
  margin-bottom: var(--mb-2-5);
}

.footer__social {
  display: flex;
  grid-column-gap: 0.75rem;
  -webkit-column-gap: 0.75rem;
          column-gap: 0.75rem;
}

.footer__social-link {
  display: inline-flex;
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );
  background: var(--container-color);
  padding: 0.25rem;
  border-radius: 0.25rem;
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
  font-size: 1rem;
}

.footer__social-link:hover {
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 50%) 0%,
    hsl(58, 28%, 40%) 100%
  );
  background: var(--body-color);
}

.footer__title {
  font-size: 1rem;
  font-size: var(--h3-font-size);
  margin-bottom: 1rem;
  margin-bottom: var(--mb-1);
}

.footer__links {
  display: flex;
  flex-direction: column;
  grid-row-gap: 0.35rem;
  row-gap: 0.35rem;
}

.footer__link {
  font-size: 0.813rem;
  font-size: var(--small-font-size);
  color: hsl(14, 4%, 85%);
  color: var(--text-color);
  transition: 0.3s;
}

.footer__link:hover {
  color: hsl(14, 4%, 100%);
  color: var(--title-color);
}

.footer__copy {
  display: block;
  text-align: center;
  font-size: 0.75rem;
  font-size: var(--smaller-font-size);
}

.scroll__up {
  position: absolute;
  right: 2rem;
  bottom: 2rem;
  padding: 3px 3px 1px 3px;
  background: linear-gradient(
    90deg,
    hsl(104, 28%, 35%) 0%,
    hsl(58, 28%, 35%) 100%
  );
  background: var(--container-color);
  cursor: pointer;
  z-index: 9999;
}

@media screen and (min-width: 320px) {
  .container {
    margin-left: 1rem;
    margin-left: var(--mb-1);
    margin-right: 1rem;
    margin-right: var(--mb-1);
  }
}

@media screen and (max-width: 600px) {
  .home__details-img {
    top: 1rem;
  }
}

@media screen and (min-width: 600px) {
  .footer__container {
    grid-row-gap: 2rem;
    grid-row-gap: 2rem;
    row-gap: 2rem;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
  }
  .footer__img-two {
    left: 16%;
    z-index: 1;
  }
}

@media screen and (min-width: 750px) {
  .nav__img,
  .nav__close,
  .nav__toggle {
    display: none;
  }

  .nav__list {
    flex-direction: row;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
  }

  .nav__link {
    text-transform: capitalize;
    font-weight: 600;
  }

  .button--ghost {
    padding: 0.34rem 1rem;
    transition: 0.3s;
  }

  .button--ghost:hover {
    color: hsl(14, 91%, 54%);
    color: var(--first-color);
    border: 2px solid hsl(14, 4%, 100%);
    border: 2px solid var(--title-color);
  }

  .nav {
    height: 4rem;
  }

  .home__content {
    display: flex;
    align-items: center;
    padding: 30px 0;
  }

  .section_title {
    font-weight: 1.5rem;
    font-weight: var(--h1-font-size);
  }

  .home__group {
    padding: 0;
  }

  .home__img {
    height: auto;
    -webkit-transform: translateY(-2rem);
            transform: translateY(-2rem);
  }
  .home__indicator {
    top: auto;
    top: initial;
    right: auto;
    right: initial;
    bottom: 14%;
    left: 45%;
  }
  .home__indicator::after {
    top: 0;
    height: 75px;
  }
  .home__details-img {
    bottom: 0;
    right: 58%;
  }
}

@media screen and (min-width: 1100px) {
  .footer__img-two {
    left: 0;
  }

  .container {
    margin: 0 auto;
  }
}

